.App {
  font-family: 'Ubuntu', sans-serif;
}


.header-wrapper {
    width: 90vw;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 100px;
    margin: 0 auto;
    padding-bottom: 0.5rem;
}

.logo img {
    height: 100%;
}

.nav {
    display: flex;
    align-items: center;
}

.nav-list {
    list-style: none; 
    display: flex;  
    flex-direction: row;
    grid-gap: 20px;
    gap: 20px;
    align-items: center;
}

.nav-link {   
    font-family: 'Ubuntu', sans-serif;
    font-size: 1.5rem;
    text-decoration: none;
    color: #4f4f4f;

}

.nav-link:hover {
   font-weight: bold;
}

.nav-link:active {
    color:#7ccbc8;
}

.cart {
    position: relative;
}

.img-cart {
    height: 70%;
    display: block;
    padding: 25px;
}

.cart:hover {
    cursor: pointer;
}

.cart-counter {
    position: absolute;
    right: 15%;
    top: 15%;
    font-weight: 600;
    font-family: 'Ubuntu', sans-serif;
    background-color: #7ccbc8;
    border-radius: 2.5em;
    width: 2rem;
    height: 2rem;
    font-size: 1.5rem;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
}


.card {
    display: flex;
    flex-direction: column;
    width: 30%;
    height: -webkit-max-content;
    height: max-content;
    box-shadow: 0 0 1em rgba(124, 203, 200, .8);
    transition: 0.3s;
} 

.card:hover {
    box-shadow: 0 0 1em rgba(0, 0, 0, .8);
}

.card__photo {
    display: flex;
    box-sizing: content-box;
    position: relative;
    background: black;
}

.card__photo:hover .card__photo_img {
    opacity: 0.4;
    transition: opacity .7s;
}

.card__photo:not(:hover) .card__photo_img {
    opacity: 1;
    transition: opacity .7s;
}

.card__photo:hover .loupe_container,
.card__photo:hover .loupe_img {
    opacity: 1;
    transition: opacity .7s;
    cursor: pointer;
}

.card__photo:not(:hover) .loupe_container,
.card__photo:not(:hover) .loupe_img {
    opacity: 0;
    transition: opacity .4s;
}

.loupe_container {
    position: absolute;
    top: calc(50% - 1rem);
    left: calc(50% - 1rem);
    width: 2rem;
    height: 2rem;
    border-radius: 1rem;
    background: #7ccbc8;
    opacity: 0;
}

.loupe_img {
    width: 1rem;
    height: 1rem;
    position: relative;
    top: 25%;
    left: 25%;
    opacity: 0;
}

.card__photo_img {
    width: 100%;
}


.card__description {
    display: grid;
    grid-template-areas: 
    "A A"
    "B B"
    "C D";
    grid-template-columns: 1fr 1fr;
    font-family: 'Ubuntu', sans-serif;
    font-size: 1.5rem;
}

.card__description_name {
    grid-area: A;
    text-align: center;
    margin: 5% 0 2% 0; 
}

.card__description_price {
    grid-area: C;
    text-align: start;
    margin: 5% 0 5% 10%;
}

.card__description_more {
    grid-area: B;
    text-align: center;
    -webkit-text-decoration: underline 1px;
            text-decoration: underline 1px;
    font-family: 'Ubuntu', sans-serif;
    font-size: smaller;
    margin: 2%;
}

.card__description_more:hover {
    cursor: pointer;
    font-weight: 700;
}

.inCart {
    grid-area: D;
    margin: 5% 10% 5% 0;
    text-align: end;
}

.add {
    grid-area: D;
    border: none;
    border-radius:40px;
    width: 40px;
    height: 40px;
    align-self: center;
    justify-self: end;
    margin-right: 10%;
    background: none;
    font-family: 'Ubuntu', sans-serif;
    margin: 0 10% 5% 0;
}

.add:hover {
    cursor: pointer;
    background-color: #7ccbc8;
}

.add-img {
    width: 30px;
    float: right;
}
.modal-overlay {
    z-index: 1;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: rgba(0,0,0,.6);
}

.modal-window {
    z-index: 2;
    position: absolute;
    top: 0; left: 0; bottom: 0; right: 0;
    margin: auto;
    display: flex;
    align-items: center;
}

.modal-body {
    z-index: 3;
    position: relative;
    margin: auto;
    display: flex;
    flex-direction: row;
    width: -webkit-max-content;
    width: max-content;
    height: 90vh;
    grid-gap: 10px;
    gap: 10px;
    background: #FFF;
    border-radius: 15px;
    justify-content: center;
    align-items: center;
}

.scroll-off {
    overflow: hidden;
}

.modal-close {
    cursor: pointer;
    position: absolute;
    width: 30px;
    height: 30px;
    right: 15px;
    top: 15px;
    border: 2px solid #7ccbc8;
    border-radius: 100px;
    background: none;
    font-size: 25px;
}

.modal-close:hover {
    background: rgba(124, 203, 200, .8);
 }

 .modal__img {
    position: relative;
    left: 1%;
    object-fit: cover;
    width: 90%;
    height: 90%;
 }
 
 .modal__content {
    font-family: 'Ubuntu', sans-serif;
    width: 30vh;
    text-align: center;
 }

 .modal-add {
    z-index: 5;
    border-radius: 30px;
    border: 1px solid #7ccbc8;
    width: 70px;
    height: 35px;
    align-self: center;
    justify-self: center;
    background-color: #7ccbc8;
    font-family: 'Ubuntu', sans-serif;
    font-size: 1em;
    margin-top: 50px;
}

.modal-add:hover {
    cursor: pointer;
    background-color: aquamarine;
}

#search-field {
    font-size: 1.2rem;
    width: 100%;
}

#search-submit {
    font-size: 1.2rem;
    width: 100%;
    margin-top: 1rem;
}

.theme-filter, .modules-filter, .width-filter, .height-filter, .price-filter {
    display: flex;
    flex-direction: column;
    font-family: 'Ubuntu', sans-serif;
    color: #4f4f4f;
}
.header-title {
    height: 50px;
    margin-top: 1rem;
    display: flex;
    align-items: center;
    justify-content:flex-start;
    background-color: #7ccbc8;
    background-size: cover;
    font-family: 'Ubuntu', sans-serif;
    text-align: start;
    padding-left: 5vw;
    color: #4f4f4f;
}

.header-title h3, .header-title h3 a {
    text-decoration: none;
    color: #4f4f4f;
    font-size: 1.5rem;
    letter-spacing: 1px;
}

.header-title h3 a:hover {
    color: #111;
}
.wrapper-main {
    width: 90vw;
    margin: 0 auto;
    display: grid;
    grid-gap: 10px;
    gap: 10px;
    grid-template-areas:
    "A B"
    "A C";
}
    
.main-filters {
    grid-area: A;
    padding-top: 30px;
    display: flex;
    flex-direction: column;
    grid-gap: 30px;
    gap: 30px;
    width: 190px;
    font-size: 1.5rem;
    
}

.main-cards {
    grid-area: B;
    padding-top: 30px;
    display: flex;
    flex-wrap: wrap;
    grid-row-gap: 30px;
    row-gap: 30px;
    grid-column-gap: 3%;
    -webkit-column-gap: 3%;
            column-gap: 3%;
    justify-content: center;
}


.div_clear_filters, .div_clear_cart, .div_clear_all {
    display: flex;
    justify-content: center;
}

.load_all, .clear_filters, .clear_cart, .clear_all {
    width: 150px;
    height: 40px;
    border-radius: 5px;
    font-family: 'Ubuntu', sans-serif;
    font-size: 1em;
    font-weight: 400;
    background-color: #7ccbc8;
    margin: 0;
}

.load_all:hover, .clear_filters:hover, .clear_cart:hover, .clear_all:hover {
    cursor: pointer;
    font-weight: 600;
    background-color: #4f4f4f;
    color: #7ccbc8;
    box-shadow: 0 0 0.5em rgba(0, 0, 0, .8);
}
.home_wrapper {
    width: 90vw;
    display: flex;
    flex-direction: row;
    grid-gap: 2rem;
    gap: 2rem;
    margin: 0 auto;
    padding-top: 3rem;
    max-height: calc(100vh - 100px);
}

.home_text_block {
    width: 70%;
    padding-top: 7rem;
}

.home_text_block h1 {
    font-size: 4rem;
    color: #4f4f4f;
}

.home_text_block p {
    font-size: 1.5rem;
    text-align: justify;
    line-height: 2rem;
    color: #619e9d;
}


.home_img img {
    height: 700px;
    object-fit: contain;
    border-radius: 10px;
}

.home_button {
    padding: 0.75rem 1.5rem;
    font-family: 'Ubuntu', sans-serif;
    font-size: 1.2rem;
    font-weight: 400;
    background-color: #7ccbc8;
    margin: 0;
    border-radius: 0.25rem;
    border: none;
}

.home_button:hover {
    cursor: pointer;
    background-color: #4f4f4f;
    color: #7ccbc8;
    box-shadow: 0 0 0.5em rgba(0, 0, 0, .8);
    transition: all .5s;
}
.about_wrapper {
    width: 90vw;
    margin: 2rem auto;
    display: grid;
    grid-template-areas: "A B";
    grid-template-columns: 1fr 1fr;
    grid-gap: 4rem;
    gap: 4rem;
    max-height: calc(100vh - 100px);
}

.about_img {
    padding-top: 2rem;
}

.about_img img{
    width: 100%;
    height: 600px;
    object-fit: cover;
    border-radius: 10px;
}

.about_text_block {
    padding-top: 2rem;
}

.about_text_block h1 {
    font-size: 3rem;
    color: #4f4f4f;
}

.about_text_block p {
    font-size: 1.2rem;
    text-align: justify;
    line-height: 2rem;
    color: #619e9d;
}
.product_wrapper {
    width: 90vw;
    margin: 0 auto;
}

.product_content_wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
    margin: 2rem auto;
}

.product_img img{
    height: 500px;
    object-fit: contain;
}

.product_info {
    width: 300px;
    display: grid;
    color: #4f4f4f;
    grid-template-columns: 200px 1fr;
    font-size: 1.5rem;
    margin-bottom: 1.25rem;
}

.product_price {
    color: #7ccbc8;
    font-size: 1.8rem;
}

.product_name {
    font-size: 3rem;
    color: #4f4f4f;
    margin-top: 0;
}

.cart_button, .back_button {
    padding: 0.5rem 1.2rem;
    font-family: 'Ubuntu', sans-serif;
    font-size: 1rem;
    font-weight: 400;
    background-color: #7ccbc8;
    margin: 0;
    border-radius: 0.25rem;
    border: none;
}

.cart_button:hover, .back_button:hover {
    cursor: pointer;
    background-color: #4f4f4f;
    color: #7ccbc8;
    box-shadow: 0 0 0.5em rgba(0, 0, 0, .8);
    transition: all .5s;
}

.button_container {
    padding-top: 1.5rem;
    display: grid;
    grid-column-gap: 3em;
    -webkit-column-gap: 3em;
            column-gap: 3em;
    grid-template-columns: 10rem 1fr;
    grid-template-areas: 
    "A B"
    "C C";
}

.amount_buttons_container {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 1rem;
    justify-content: space-between;
}

.amount_buttons_container h2 {
    margin: 0;
    color: #4f4f4f;
    font-size: 2rem;
}

.amount_button {
    background: transparent;
    border-color: transparent;
    color: #4f4f4f;
    cursor: pointer;
    padding: 1rem 0px;
    font-weight: bold;
    width: 4rem;
    height: 2rem;
    font-size: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.back {
    padding-top: 3rem;
}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

